import React, { useEffect, useState } from "react";
import Reader from "./Reader";
import "./Page.css"
import { toast } from "react-toastify";
import { toastProp, loggingId } from "../Util";
import { SEARCH_PER_SCREEN, MAX_SEARCH_ENTRY, getBookState, getUserState, toUtf8 } from "../Util";
import { useQuery, useLazyQuery } from "@apollo/client";
import { Link } from 'react-router-dom'
import {USERS_QUERY, HISTORY_QUERY} from "../api/query.js";
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import ListView from "../ListView";

const State = {
    LoggedOut: 0,
    LoggingIn: 1,
    LoggedIn:  2
}

function RentHistory(props) {
    const [userText, setUserText] = useState("");
    const [passwordText, setPasswordText] = useState("");
    const [rentList, setRentList] = useState([]);
    const [initialized, setInitialized] = useState(false);
    const [userId, setUserId] = useState("");
    const [state, setState] = useState(State.LoggedOut);
    const [history, setHistory] = useState([]);
    const {loading: userLoading, data: userListData, error: userError } = useQuery(USERS_QUERY);
    const [loadHistory, { data: historyData }] = useLazyQuery(HISTORY_QUERY,
                     { "variables": { "user_id": userId } });
    const [expireDate, setExpireDate] = useState("");
    const [title, setTitle] = useState("");
    const [pageNum, setPageNum] = useState(0);
    const [displayList, setDisplayList] = useState([]);
    const [userList, setUserList] = useState({});

    useEffect(function () {
        async function initialize() {
            if (props.doc.isOpen())
                updateDoc();
            else
                props.doc.setCallback(updateDoc);
            console.log("=======================================");
            console.log("RentHistory initialize");


            const date = new Date();
            const days=2;
            date.setTime(date.getTime()+(days*24*60*60*1000));
            setExpireDate(date.toGMTString());

        }

        initialize();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(
        () => {
            console.log("User list loaded");
            console.log(userError);
            console.log(userLoading);
            if (userListData)
            {
                var list = [];
                console.log("User list available");
                const stateStr = {"0": props.text.normal, "1": props.text.overDue, "2": props.text.stopped};
                for (let i = 0; i < userListData.users.length; i++)
                {
                    const user = userListData.users[i];
                    const entry = {"id": user._id, "name": user.name, "level": user.level, "state": getUserState(props.text, user.state)};
                    list.push(entry)
                }
                props.doc.setUser(list);
                setUserList(props.doc.getUser());
            }
        },
        [userListData, userLoading, userError]
    );

    useEffect(
        () => {
            let rawHist = [];
            if (!historyData || !props.doc.bookReady || !props.doc.rentReady)
                return;
            console.log("History updated ");
//            console.log(historyData);
            for (let i = 0 ; i < historyData["rentLogs"].length ; i++)
            {
                const entry = historyData["rentLogs"][i];
                if (entry["book_state"] !== "0" && entry["book_state"] !== "1")
                    continue;
                const id = entry["book_id"];
                const date = entry["timestamp"].split(" ")[0].replace("-", "/", 2).replace("-", "/")
                rawHist.push({"id": id, "title": props.doc.book[id]["title"], "date": date, "state": entry["book_state"]});
            }
            rawHist.sort((s1, s2) => { return s1["date"] > s2["date"]; });
//            console.log(rawHist);

            let hist = [];
            for (let i = 0 ; i < rawHist.length - 1 ; i++)
            {
                if (rawHist[i]["state"] !== "1")
                    continue;
                const entry  = rawHist[i];
                const id = entry["id"];
                for (let j = i+1 ; j < rawHist.length ; j++)
                {
                    if (rawHist[j]["state"] !== "0" || id !== rawHist[j]["id"])
                        continue;
                    hist.push({"id": entry["id"], "title": entry["title"], "rentDate": entry["date"], "retDate": rawHist[j]["date"]});
                    break;
                }
            }
//            console.log(hist);
            console.log("Set history");
            setHistory(hist);
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [historyData, props.doc.bookReady, props.doc.rentReady]
    );

    function compare(a1, a2)
    {
        return a1.rentDate > a2.rentDate;
    }

    async function updateDoc()
    {
        console.log("All data loaded " + initialized);
        /*
        if (props.doc.logged)
        {
            setState(State.LoggedIn);
            const userId = props.doc.userInfo['_id'];
            setUserId(userId);
            setRentList(props.doc.getRent(userId));
            await loadHistory();
        }
        else
        {
            setState(State.LoggedOut);
            setUserId("");
            setPasswordText("");
        }
        */
        const rent = props.doc.getRent();
        rent.sort(compare);
        setRentList(rent);

        console.log("Done");
        setInitialized(true);
    }

    const showRented = (rent, index) => {
        const id = rent["id"];
        const rentDate = rent["rentDate"];
        const retDate = rent["retDate"];
        const bookName = rent["title"];
        const userId = rent["user"];
        var userName;
        const key = index.toString();
        if (userList && userId in userList)
            userName = userList[userId].name;
        else
            userName = ""
        return (<React.Fragment key={key + "Fragment"}>
                    <tr key={key} className="bookTop">
                        <td className="bookCell"><Link to={"/search/"+id}>{id}</Link></td>
                        <td className="bookCell">{rentDate}</td>
                        <td className="bookCell">{retDate}</td>
                    </tr>
                    <tr key={key + "Renter"} className="bookRow">
                        <td className="bookCell"> {rent["user"]} </td>
                        <td colSpan="2" className="bookCell"> {userName} </td>
                    </tr>
                    <tr key={key + "Title"} className="bookBottom">
                        <td colSpan="3" className="bookCell">{bookName}</td>
                    </tr>
                </React.Fragment>
                );
    }

    const showEntries = (result) => {
        return (<div>
                    <table><tbody>
                    <tr key="ID">
                        <th id="id">{props.text.id}</th>
                        <th id="rentDate">{props.text.rentDate}</th>
                        <th id="returnDate">{props.text.returnDate}</th>
                    </tr>
                    {
                        result &&
                        result.map((rent, index) => {
                            return showRented(rent, index);
                        })
                    }
                    {
                        result && result.length === 0 && <tr key="None"><td colSpan="3">{props.text.noEntry}</td></tr>
                    }
                    </tbody></table>
                </div>);
    }

    useEffect(
        () => {
            const length = rentList.length;
                console.log("Page num " + pageNum.toString());
            if (length > SEARCH_PER_SCREEN)
            {
                const startIdx = pageNum * SEARCH_PER_SCREEN;
                const count = Math.min(SEARCH_PER_SCREEN, length - startIdx);
                setDisplayList(rentList.slice(startIdx,startIdx+count));
            }
            else
            {
                setDisplayList(rentList);
                setPageNum(0);
            }
        }, [rentList, pageNum]
    );

    function movePrev() {
        if (pageNum > 0)
        {
            setPageNum(pageNum - 1);
        }
    }

    function moveNext() {
        if (rentList.length > (pageNum + 1) * SEARCH_PER_SCREEN)
        {
            setPageNum(pageNum + 1);
        }
    }
/*
                <div>
                    { showEntries(displayList) }
                </div>
                {rentList.length > SEARCH_PER_SCREEN && (
                    <div id="pageControl">
                        <div className="page" id = "page">
                            <NavigateBeforeIcon fontSize="large" sx={{color: (pageNum === 0) ? "#ffffff":"#404040"}} onClick={movePrev}/>
                        </div>
                        <div className="pageNum" id="pageNum">
                            {pageNum+1}
                        </div>
                        <div className="page" id = "page">
                            <NavigateNextIcon fontSize="large" sx={{color: (rentList.length <= (pageNum + 1) * SEARCH_PER_SCREEN) ? "#ffffff":"#404040"}} onClick={moveNext}/>
                        </div>
                    </div>
                )}
*/

    return (
        <div id="checkOut">
            <div id="title">
                <h2>
                    {props.text.history};
                </h2>
            </div>
            <div id="checkOutResult">
                <Link className='menu-items' to="/barcodeReader">
                    {props.text.checkOut}
                </Link>
                <ListView list={rentList} showCallback={(entries) => { return showEntries(entries); }}/>

            </div>
        </div>
    );
}
/*
                <div id="name">{props.text.history}</div>
                <div>
                    { showEntries(history) }
                </div>
*/

export default RentHistory;
